<template>
  <div class="header">
    <div class="content clearfix">
      <div class="logo">
        <a href="mainpage.html" class="goindex">
          <img class="logo-img" src="../../assets/images/logo.png" alt="低调看体育直播_世界杯直播免费看">
        </a>
        <div class="navBar">
          <a :class="navpage=='index'?'active':''" href="javascript:void(0)" @click="changeNav('index')">首页</a>
          <a
            :class="{active:(navpage=='worldCup'?true:false),wcIcon:true}"
            href="javascript:void(0)" @click="changeNav('worldCup')">
            <img v-if="navpage=='worldCup'" class="worldCup-img" src="../../assets/images/worldCup_icon.png" alt="">
            <img v-else class="worldCup-img" src="../../assets/images/worldCup_icon_n.png" alt="">
          </a>
          <a :class="navpage=='review'?'active':''" href="javascript:void(0)" @click="changeNav('review')">回看/集锦</a>
          <a href="download.html" @click="changeNav('download')">APP直播</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  export default{
    name:'navHeader',
    data(){
      return {
        navpage:'index',
      }
    },
    beforeCreate(){

    },
    created(){
      let href = window.location.href
      if(href.indexOf('review')>=0){
        this.navpage = 'review'
        this.changeNav('review',true)
      }else if(href.indexOf('worldCup')>=0){
        this.navpage = 'worldCup'
        this.changeNav('worldCup',true)
      }else{
        if(utils.getPara('page')=='index'){
          this.navpage = 'index'
          this.changeNav('index',true)
        }
      }

    },
    methods:{
      changeNav(type,iscreated){
        if(type !='download'){
          this.navpage = type
          this.$emit('changeNav',type)
        }
        if(iscreated){

        }else{
          MtaH5.clickStat('MobileEvent',{'navclick':type})
        }
      },
    }
  }
</script>
<style lang="less">
  .header {
    text-align: left;
    width: 100%;
    height: .96rem;
    line-height: .96rem;
    .logo {
      height: 100%;
      position: relative;
      width: 100%;
      height: .96rem;
      line-height: .96rem;
      background: #2f2f40;
      background: url('../../assets/images/navHeader.png') no-repeat top left;
      background-size:cover;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .goindex{
        width: 1.42rem;
        margin-left:.37rem;
        display: block;
        .logo-img {
          width: 100%;display: block;
        }
      }
      .navBar{
        flex: 1;
        text-align: right;
        box-sizing: border-box;
        padding-right: .66rem;
        line-height: .96rem;
        a{
          display: inline-block;
          margin-left: .15rem;
          font-size:.28rem;
          color:#7380b2;

          &.active{
             color: #fff;
          }
        }
        .wcIcon{
          .worldCup-img{
            width: 1rem;
            display: block;
            transform: translateY(15%) scale(1.05);
          }
        }
      }
    }
  }
</style>



