<template>
  <div class="main-page">
    <ul class="match-type">
      <li v-for="(item,index) in type" @click="changeType(item)" :class="item.active?'active':''">{{item.name}}</li>
    </ul>
    <div class="listbox">
      <match-list :ListBan="ListBan" :Ischannel="true" :MatchType="matchType"></match-list>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  import matchList from '../matchList/matchList'
  import * as utils from '../../../common/js/utils'
	import Vue from 'vue'

  export default{
    name: 'mainpage',
    components: {matchList},
    props:{
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    data() {
      return {
        more:{
          moreShow:false,
        },
        type:[
          {name:'全部',type:1000,active:true},
          {name:'足球',type:1002,active:false},
        	/*{name:'热门',type:1100,active:false},*/
        	{name:'篮球',type:1001,active:false},
        	/*{name:'其他',type:1009,active:false},*/
        ],
        matchType:1000,
        channelList:[],
        showdownlink:false,

        mainStyle:{
         paddingBottom: '1.13rem'
        },
        isqd:'',

        navpage:true // true 为index 、false 为review
      }
    },
    created(){
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):''
      sessionStorage.clear();
      document.getElementById('loading').style.display = 'none';
    },
    methods: {
      getChannelList(){
        let toData = {
          url: 'channel/list',
          callback: (res) => {
            if (res.error_code === 1) {
              this.channelList = res.data;
            }
          },
          error: () => {
            console.log('网络错误')
          }
        }
        utils.cpost(this, toData);
      },
      changeType(item){
      	this.type.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);
        this.matchType = item.type;
        switch (item.type){
          case 1000:
            MtaH5.clickStat('MobileEvent',{'matchtype':'allmatch'})
            break;
          case 1002:
            MtaH5.clickStat('MobileEvent',{'matchtype':'football'})
            break;
          case 1001:
            MtaH5.clickStat('MobileEvent',{'matchtype':'basketball'})
            break;
        }
      },
      isencode(src){
        if(src.indexOf('rtmp')>-1&&src.indexOf('.html')<0){
          return src
        }else{
          return encodeURIComponent(src)
        }
      },

    },
  }
</script>

<style lang="less">
@import "../../../common/css/mainpage.less";
</style>



