<template>
  <div class="appPopup" @click="goLink()" v-show="show&&showPopup">
    <img :src="img" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播" />
    <a href="javascript:void(0)" @click.stop="clBtn"></a>
  </div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  export default{
    name:'appPopup',
    props:{
      Banner:{
        type:Object,
        default:{
          iosShow:false,androidShow:false
        }
      }
    },
    data(){
      return{
        showPopup:true,
        isIOS:true,
      }
    },
    computed:{
      show:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.Banner.androidShow
        }else{
          return this.Banner.iosShow
        }
      },
      img:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.Banner.androidImg
        }else{
          return this.Banner.iosImg
        }
      },
      url:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.Banner.androidUrl
        }else{
          return this.Banner.iosUrl
        }
      }
    },
    created(){
       if(utils.checkDevice().versions.android){
         this.isIOS = false
       }
    },
    methods:{
      goLink(){
        if(this.isIOS){
          MtaH5.clickStat('moblieSuspend',{'phone':'ios'})
         /* window.location.href = 'https://itunes.apple.com/app/apple-store/id1364005432?pt=119004768&ct=007daoliang&mt=8'*/
        }else{
          MtaH5.clickStat('moblieSuspend',{'phone':'android'})
         /* window.location.href = 'http://m.007kq.com/download.html'*/
        }
        window.location.href = this.url
      },
      clBtn(){
        this.showPopup = false;
        MtaH5.clickStat('moblieSuspend',{'close':'true'})
      },
      is_weixn(){
        var ua = navigator.userAgent.toLowerCase();
        if(ua.match(/MicroMessenger/i)=="micromessenger") {
          return true;
        } else {
          return false;
        }
      },
    }
  }
</script>
<style>
  .appPopup{
    position: fixed;
    top:65%;
    right:.3rem;
    right:0;
    z-index: 9999;
    cursor: pointer;
  }
  .appPopup img{
    width: 1.3rem;
  }
  .appPopup a{
    display: block;
    width: 30%;
    height: 20%;
    /*background: rgba(0,0,0,.5);*/
    position: absolute;
    right:0;
    top:0;
  }
</style>



