<template>
  <div>
    <ul class="main">
      <li v-for="game in gamelist" class="item-container">
        <div v-if="game.type=='date'" class="title">
          <img src="../../../assets/images/dateIcon.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播"/>
          <span>{{getDateWeek(game.matchBeginTime)}}</span>
        </div>
        <div :class="game.hot=='true'?'item-match-box bold':'item-match-box'">
          <!--<a :href="'replayer.html?ADTAG='+isqd+'&matchId='+game.matchId+'&title='+(game.leagueName?game.leagueName:'低调看直播_Jrs直播_西甲直播_NBA直播吧_英超直播_007看球_高清免费')" class="firsr-a"></a>-->
          <a
            :href="'replayer.html?ADTAG='+isqd+'&matchId='+game.matchId+'&title='+(game.leagueName?game.leagueName:'低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播')"
            class="list-item flexBox" target="_blank"
            style="cursor: pointer;">
            <!--比赛-->
            <div class="game flexBox">
              <div class="game-name">
                <div class="iconImg">
                  <div class="time"><span>{{getMatchDate(game.matchBeginTime)}}</span></div>
                </div>
                <div class="name">{{game.leagueName?game.leagueName.substring(0,10):game.leagueName}}</div>
              </div>
              <div class="game-info">
                <template v-if="game.awayLogo">
                  <div class="team">
                    <div class="teamInfo">
                      <img v-lazy="game.homeLogo" alt="" />
                      <span>{{game.homeName}}</span>
                    </div>
                    <div class="teamInfo">
                      <img v-lazy="game.awayLogo" alt="" />
                      <span>{{game.awayName}}</span>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <div class="special-game">
                    <div class="show-content">
                      <img v-lazy="game.homeLogo" alt="" />
                      <div class="word">{{game.homeName}}</div>
                    </div>
                  </div>
                </template>
              </div>
              <!---1:已结束，0未开始，1，即将开始(提前十分钟)，2正在直播，-->
              <div class="playeIcon">
                <img src="../../../assets/images/review_play.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
                <span>回看 / 集锦</span>
              </div>
            </div>
          </a>
        </div>
        <listBan :ListBan="ListBan" v-if="game.banner"></listBan>
      </li>
      <li class="loadmore" v-show="nomorelist">
        <div class="nodata" v-if="nodata"></div>
        <a v-else href="javascript:void (0)">
          <img src="../../../assets/images/loading.gif" class="loading" v-if="loadTxt=='正在加载中…'"/>
          <span>{{ loadTxt }}</span>
        </a>
      </li>
      <li v-if="nodata">
        <div class="nodata" v-if="nodata"><img src="../../../assets/images/nodata.png" alt="低调看直播_Jrs直播_西甲直播_NBA直播吧_英超直播_007看球_高清免费"/></div>
      </li>
    </ul>
  </div>
</template>
<style scoped>
  @import "review.less";
</style>
<script>
  import * as utils from '../../../common/js/utils'
  import listBan from '../listBan.vue'
  export default{
    name:'review',
    props:{
      MatchType:Number,
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    components:{listBan},
    data(){
      return{
        page:{
          start:0,
          pageSize:15
        },
        matchType:1002,
        gamelist:[],
        nodata:false,
        nomorelist:true,
        loadTxt:'正在加载中',
        isqd:'',
        isReview:true,
        // scroll
        scrollState:true,
        canload:true,

      }
    },
    created(){
      /*console.log(this.MatchType)*/
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):''
      this.initData();
      this.scrollload();
    },
    watch:{
      MatchType:function (val,oldval) {
        this.canload = true;
        this.initData();
        if(val!=oldval){
          this.nodata = false;
        }
      }
    },
    methods:{
      changeType(item){
        this.type.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);
        this.matchType = item.type;
      },

      // test
      initData(){
        this.page= {
          start:0,
          pageSize:15,
          type : this.MatchType
        };
        this.loadTxt ='正在加载中…';
        let toData = {
          url:'game/endList',
          postData:this.page,
          callback:(res)=>{
            if (res.error_code == 1) {
              let list = res.data;
              this.oldTime = list[0].matchBeginTime

              this.gamelist = this.joinDatalist(list);
              this.gamelist[0].type = 'date'
              if(list.length>=7){
                this.gamelist[6].banner = true
              }

              if(list.length>0&&list.length<this.page.pageSize){
                this.nomorelist = false;
                this.nodata = false;
              }else if(list.length==0){
                this.nomorelist = true;
                this.loadTxt = '暂时没有数据'
                this.nodata = true;
              }
            }
          },
          error:()=>{
            console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      },

      scrollload(){
        var _that = this;
        window.onscroll = function (event) {
          if(_that.isReview){
            var wScrollY = window.scrollY; // 当前滚动条位置
            var wInnerH = window.innerHeight; // 设备窗口的高度（不会变）
            var bScrollH = document.body.scrollHeight; // 滚动条总高度
            /*console.log('get in scrollload')*/
            if (wScrollY + wInnerH >= bScrollH-50&&_that.scrollState&&_that.canload) {
              /*console.log(wScrollY + '--' + wInnerH +'+=='+(wScrollY + wInnerH) + '--' + bScrollH)*/
              _that.scrollState = false;
              _that.loadmore();
              setTimeout(function () {
                _that.scrollState = true;
              },1000)
            }
          }
        }
      },
      loadmore(){
        this.nomorelist = true;
        if(this.canload){
          this.page.start = this.gamelist.length;
          let param = this.page;
          param.type = this.MatchType
          this.loadTxt ='正在加载中…';
          let toData = {
            url:'game/endList',
            postData:param,
            callback:(res) => {

              if (res.error_code === 1) {
                if(res.data.length>0){
                  let list = this.joinDatalist(res.data);
                  this.gamelist.push(...list);
                  this.nomorelist = false;
                  if(res.data.length<15){
                    this.canload = false;
                    this.nomorelist = true;
                    this.loadTxt = '没有更多数据了';
                  }
                }else{
                  this.canload = false;
                  this.nomorelist = true;
                  this.loadTxt = '没有更多数据了';
                }
              }
            },

          }
          utils.cpost(this,toData);
        }
      },
      joinDatalist(list){
        /*console.log(list)*/
        list.map((item,index)=>{
          item.type = 'macth'
//          console.log(this.oldTime.split(' ')[0],item.matchBeginTime.split(' ')[0],this.oldTime.split(' ')[0]==item.matchBeginTime.split(' ')[0])
          if(this.oldTime.split(' ')[0]!=item.matchBeginTime.split(' ')[0]) {
            if(item.awayLogo){
              this.oldTime = item.matchBeginTime
              item.type = 'date'
            }else{
              item.type = 'macth'
            }
          }

          /*if(index== 6 || index == 13){
            item.banner = true
          }else{
            item.banner = false
          }*/

        })

        return list
      },
      getMatchDate(time){
        if(time){
          let str = time.split(' ')[1];
          let arr = str.split(':');
          arr.pop();
          time = arr.join(':');
        }
        return time
      },
      getDateWeek(time){
        if(time){
          let ymd = time.split(' ')[0]
          let iosd = time.replace(/-/g,'/');
          let d = (new Date(iosd)).getDay();
          let strd = ''

          switch (d){
            case 0:
              strd = '星期日';
              break;
            case 1:
              strd = '星期一';
              break;
            case 2:
              strd = '星期二';
              break;
            case 3:
              strd = '星期三';
              break;
            case 4:
              strd = '星期四';
              break;
            case 5:
              strd = '星期五';
              break;
            case 6:
              strd = '星期六';
              break;
          }

          let str = ymd + ' ' + strd
          return str
        }
      }
    },
    beforeDestroy(){
      this.isReview = false
    }
  }
</script>



