<template>
  <div class="review">
    <ul class="main">
      <li v-for="(item,key,index) in gamelist" class="item-container">
        <div class="title">
          <img src="../../../assets/images/dateIcon.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播"/>
          <span>{{getDateWeek(key)}}</span>
        </div>
        <div v-for="(game,index) in item" :class="game.hot=='true'?'item-match-box bold':'item-match-box'">
          <a
            :href="'replayer.html?ADTAG='+isqd+'&matchId='+game.matchId+'&type=worldCup'+'&title='+(game.leagueName?game.leagueName:'jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播')"
            class="list-item flexBox" target="_self"
            style="cursor: pointer;">
            <!--比赛-->
            <div class="game flexBox">
              <div class="game-name">
                <div class="iconImg">
                  <div class="time"><span>{{getMatchDate(game.matchBeginTime)}}</span></div>
                </div>
                <div class="name">{{game.leagueName?game.leagueName.substring(0,10):game.leagueName}}</div>
              </div>
              <div class="game-info">
                <template v-if="game.awayLogo">
                  <div class="team">
                    <div class="teamInfo">
                      <img v-lazy="game.homeLogo" alt="" />
                      <span>{{game.homeName}}</span>
                    </div>
                    <div class="teamInfo">
                      <img v-lazy="game.awayLogo" alt="" />
                      <span>{{game.awayName}}</span>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <div class="special-game">
                    <div class="show-content">
                      <img v-lazy="game.homeLogo" alt="" />
                      <div class="word">{{game.homeName}}</div>
                    </div>
                  </div>
                </template>
              </div>
              <!---1:已结束，0未开始，1，即将开始(提前十分钟)，2正在直播，-->
              <div id="playeIcon">
                <img style="width: .28rem;" src="../../../assets/images/review_play.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
                <span>回看 / 集锦</span>
              </div>
            </div>
          </a>
          <listBan :ListBan="ListBan" v-if="game.banner"></listBan>
        </div>
      </li>
      <li class="loadmore" v-show="nomorelist">
        <div class="nodata" v-if="nodata"></div>
        <a v-else href="javascript:void (0)">
          <img src="../../../assets/images/loading.gif" class="loading" v-if="loadTxt=='正在加载中…'"/>
          <span>{{ loadTxt }}</span>
        </a>
      </li>
      <li v-if="nodata">
        <div class="nodata" v-if="nodata">
          <img src="../../../assets/images/nodata.png" alt="低调看直播_Jrs直播_西甲直播_NBA直播吧_英超直播_007看球_高清免费"/>
        </div>
      </li>
    </ul>
  </div>
</template>
<style scoped>
  #playeIcon{
    position: absolute;
    display: block;
    border-radius: .21rem;
    text-align: center;
    font-size: .22rem;
    top:50%;
    transform: translateY(-50%);
    right:.50rem;
    padding:.1rem 0;
    color:#4154c5;
  }
  #playeIcon img{
    width: .28rem;
  }
</style>
<script>
  import * as utils from '../../../common/js/utils'
  import listBan from '../listBan.vue'
  export default{
    props: {
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    components: {listBan},
    data() {
      return {
        gamelist: [],
        nodata: false,
        nomorelist: true,
        loadTxt: '正在加载中…',
        isqd: '',
        // scroll
        scrollState:true,
        canload:true,

        startTime:'2018-06-14',
        endTime:'2018-07-15',
        isWorlCupReview: true,
        page: [],
        currentPage: 1
      }
    },
    created() {
      this.isqd = utils.getPara('ADTAG') ? utils.getPara('ADTAG') : ''
       this.page = [
        { startTime: '2018-06-14', endTime: '2018-06-20' },
        { startTime: '2018-06-21', endTime: '2018-06-27' },
        { startTime: '2018-06-28', endTime: '2018-07-04' },
        { startTime: '2018-07-05', endTime: '2018-07-11' },
        { startTime: '2018-07-05', endTime: '2018-07-11' },
        { startTime: '2018-07-12', endTime: '2018-07-15' },
      ]
      this.initData()
      this.scrollload()
    },
    beforeDestroy() {
      this.isWorlCupReview = false
    },
    methods: {
      initData(page) {
        let params = {
          startTime: '2018-06-14',
          endTime: '2018-06-20'
        }
        let toData = {
          url: 'worldcup/v2/list',
          postData: params,
          callback: (res) => {
            if (res.error_code === 1) {
              this.nomorelist = false;
              this.nodata = false;
              this.gamelist = res.data
            }
          },
          error: () => {
            console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      },
      scrollload() {
        var _that = this;
        window.onscroll = function (event) {
          if(_that.isWorlCupReview){
            var wScrollY = window.scrollY; // 当前滚动条位置
            var wInnerH = window.innerHeight; // 设备窗口的高度（不会变）
            var bScrollH = document.body.scrollHeight; // 滚动条总高度
            if (wScrollY + wInnerH >= bScrollH-50&&_that.scrollState&&_that.canload) {
              _that.scrollState = false;
              _that.loadmore();
              setTimeout(function () {
                _that.scrollState = true;
              },1000)
            }
          }
        }
      },
      loadmore() {
        const params = this.page[this.currentPage]
        this.nomorelist = true
        this.loadTxt ='正在加载中…'
        if (params && this.canload) {
          let toData = {
            url: 'worldcup/v2/list',
            postData: params,
            callback: (res) => {
              if (res.error_code === 1) {
                this.nomorelist = false;
                this.nodata = false;
                for (const i in res.data ) {
                  this.$set(this.gamelist, i, res.data[i])
                }
                this.currentPage++
              }
            },
            error: () => {
              console.error('网络错误')
            }
          }
          utils.cpost(this, toData)
        } else {
          this.canload = false
          this.nomorelist = true
          this.loadTxt = '没有更多数据了'
        }

      },
      getDate(startDate,stopDate){
        Date.prototype.addDays = function(days) {
          var dat = new Date(this.valueOf())
          dat.setDate(dat.getDate() + days);
          return dat;
        }
        startDate = new Date(startDate)
        stopDate = new Date(stopDate)
        var dateArray = new Array();
        var currentDate = startDate;
        while (currentDate <= stopDate) {
          dateArray.push( new Date (currentDate) )
          currentDate = currentDate.addDays(1);
        }

        for(let i=0;i<dateArray.length;i++){
          dateArray[i] = (utils.dateFormate(dateArray[i],'yyyy-MM-dd hh:mm:ss')).split(' ')[0]
        }

        return dateArray;
      },
      getDateWeek(time){
        let str = time
        if(time){
          let ymd = time.split(' ')[0]
          let d = (new Date(time)).getDay();
          let strd = ''

          switch (d){
            case 0:
              strd = '星期日';
              break;
            case 1:
              strd = '星期一';
              break;
            case 2:
              strd = '星期二';
              break;
            case 3:
              strd = '星期三';
              break;
            case 4:
              strd = '星期四';
              break;
            case 5:
              strd = '星期五';
              break;
            case 6:
              strd = '星期六';
              break;
          }

          str = ymd + ' ' + strd
        }
        return str
      },
      getMatchDate(time){
        if(time){
          let str = time.split(' ')[1];
          let arr = str.split(':');
          arr.pop();
          time = arr.join(':');
        }
        return time
      },
    }
  }
</script>




