<template>
  <div class="review">
    <ul class="match-type">
      <li v-for="(item,index) in type" @click="changeType(item)" :class="item.active?'active':''">{{item.name}}</li>
    </ul>
    <reviewList :ListBan="ListBan" :MatchType="matchType"></reviewList>
  </div>
</template>
<style lang="less">
  @import "review.less";
</style>
<script>

  import Vue from 'vue'
  import * as utils from '../../../common/js/utils'
  import reviewList from './reviewList.vue'
  export default{
    name:'review',
    props:{
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    components:{reviewList},
    data(){
      return{
        type:[
          {name:'全部',type:1000,active:true},
          {name:'足球',type:1002,active:false},
          /*{name:'热门',type:1100,active:false},*/
          {name:'篮球',type:1001,active:false},
          /*{name:'其他',type:1009,active:false},*/
        ],

        matchType:1000

      }
    },

    created(){

    },
    methods:{
      changeType(item){
        this.type.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);
        this.matchType = item.type;
        switch (item.type){
          case 1000:
            MtaH5.clickStat('MobileEvent',{'reviewtype':'allmatch'})
            break;
          case 1002:
            MtaH5.clickStat('MobileEvent',{'reviewtype':'football'})
            break;
          case 1001:
            MtaH5.clickStat('MobileEvent',{'reviewtype':'basketball'})
            break;
        }
      },

    }
  }
</script>



