<template>

  <div class="main-page" :style="mainStyle">
    <a
      v-if="isYT"
      class="goback-nav"
      onclick="javascript:history.back(-1)">
      <img src="../../assets/images/back.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <span>低调看直播</span>
    </a>
    <nav-header v-on:changeNav="changeNav"></nav-header>

    <contact :Banner="bannerArr.contact"></contact>

    <div class="channel" v-if="navpage!='worldCup'">
      <img @click="showChannel=!showChannel" src="../../assets/images/channelIcon.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <div class="channel-list" v-show="showChannel">
        <a v-for="item in channelList"
           :href="'player.html?ADTAG='+isqd+'&isChannel=1&title='+(encodeURIComponent(item.channelName))"
           class="channel-item">{{item.channelName}}</a>
        <div class="close-channel" @click="showChannel = false;"></div>
      </div>
    </div>

    <!--<router-view/>-->
    <index-page :ListBan="bannerArr.listBan" v-if="navpage=='index'"></index-page>
    <review :ListBan="bannerArr.listBan" v-else-if="navpage=='review'"></review>
    <worldCup :ListBan="bannerArr.listBan" v-else-if="navpage=='worldCup'"></worldCup>

    <div class="footer" id="footerBottom">
      v1.2.0 <br/>
      免责声明：低调看直播是专业的体育直播导航网站，所有直播信号均由网友提供，如有异议请与我们联系。低调看直播合作联系QQ：3510317706
    </div>
    <!--:showD="showdownlink" @closeDownlink="closedownlink"-->
    <downlink :Banner="bannerArr.downlink"></downlink>
    <appPop :Banner="bannerArr.appPop"></appPop>
  </div>
</template>

<script type='text/ecmascript-6'>
  import * as utils from '../../common/js/utils'
  import downlink from '../components/downlink.vue'
  import contact from '../components/contact.vue'
  import appPop from '../components/appPop.vue'
  import indexPage from '../components/mainTemplate/indexPage.vue'
  import review from '../components/review/review.vue'
  import navHeader from '../components/navHeader.vue'
  import worldCup from '../components/worldCup/worldCup.vue'
  export default{
    name: 'mainpage',
    components: {downlink, contact,appPop,indexPage,review,navHeader,worldCup},
    data() {
      return {
        more:{
          moreShow:false,
        },
        navlist:[
          {path:''}
        ],
        channelList:[],
        showdownlink:false,

        mainStyle:{
          paddingBottom: '1.13rem'
        },
        isqd:'',

        navpage:'index',

        bannerArr:{
          downlink:{iosShow:false,androidShow:false},
          contact:{iosShow:false,androidShow:false},
          appPop:{iosShow:false,androidShow:false},
          listBan:{iosShow:false,androidShow:false},
        },
        channelList:[],
        showChannel:false,
        isYT:false,
      }
    },
    created(){
      if(utils.getPara('ADTAG')=='ytjc'){
        this.isYT = true
      }
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):''
      sessionStorage.clear();
      document.getElementById('loading').style.display = 'none';

      let href = window.location.href
      if(href.indexOf('review')>=0){
        this.navpage = 'review'
      }else if(href.indexOf('worldCup')>=0){
        this.navpage = 'worldCup'
      }

      this.getBannerData();
      this.getChannelList();
    },
    methods: {
      changeNav(type){
        this.navpage = type
      },
      closemore(){
        this.more.moreShow=false
        document.documentElement.style.overflow = "scroll"
      },
      closedownlink(type){
        this.showdownlink = type;
        if(type==false){
          this.mainStyle.paddingBottom = '0'
        }
      },
      getBannerData(){
        let toData = {
          url:'ggw/mobile/getAll',
          callback:(res)=>{
            /*console.log(res.data)*/
            if(res.error_code==1){
              let list = res.data
              //  type广告位置类型 0：底层广告 1：列表广告 2：悬浮窗广告 3：文字广告
              list.map((item,index)=>{
                switch (item.type){
                  case 0:
                    this.bannerArr.downlink = item
                    break;
                  case 1:
                    this.bannerArr.listBan = item
                    break;
                  case 2:
                    this.bannerArr.appPop = item
                  case 3:
                    this.bannerArr.contact = item
                    break;
                }
              })
            }
          }
        }
        utils.cpost(this,toData)
      },
      getChannelList(){
        let toData = {
          url: 'channel/list',
          callback: (res) => {

            if (res.error_code === 1) {
              // this.channelList = res.data;
              let list = res.data
              list.map((item,index)=>{
                if(item.videoType!="iframe"){
                  this.channelList.push(item)
                }
              });
            }
          },
          error: () => {
            console.log('网络错误')
          }
        }
        utils.cpost(this, toData);
      },
      clsChannel(){
        console.log(false)
        this.showChannel = false
      }
    },
  }
</script>

<style lang="less">
  @import "../../common/css/mainpage.less";
  .goback-nav{
    width: 100%;
    background: #000;
    height: .88rem;
    display: block;
    text-align: center;
    color:#878787;
    font-size: .26rem;
    line-height: .88rem;
    position: relative;
  img{
    width: .14rem;
    position: absolute;
    top:50%;
    left: .38rem;
    transform: translateY(-50%);
  }
  }
</style>



