<template>
  <div class="match-list" ref="listbox" :style="boxStyle">
    <review v-if="isReview"></review>
    <div v-else class="content">
      <ul class="main">
        <li v-for="game in gamelist" class="item-container">
          <div v-if="game.type=='date'" class="title">
            <div class="line"></div>
            <span>{{getDateWeek(game.matchBeginTime)}}</span>
            <div class="line"></div>
          </div>
          <div :class="game.hot=='true'?'item-match-box bold':'item-match-box'">
            <a
              v-if="game.show"
              :href="'player.html?ADTAG='+isqd+'&isChannel=3&matchId='+game.matchId+'&title='+(game.matchDesc?game.matchDesc:'低调看直播')"
              class="firsr-a">
            </a>
            <a
              v-else
              :href="'order.html?ADTAG='+isqd+'&mid='+game.mid"
               target="_self"
               class="firsr-a">
            </a>
            <a href="javascript:void(0)" class="list-item flexBox" target="_self" style="cursor: pointer;">
              <!--比赛 3为结束  4为即将开始  2进行中   1未开始  A8APP接口-->
              <div class="game flexBox">
                <div class="team">
                  <img class="teamLogo" v-if="hasLogo(game.homeLogo)" v-lazy="game.homeLogo" alt="">
                  <img class="teamLogo" v-else src="../../../assets/images/worldCup/defalut.png" alt="">
                  <div class="teamName">{{game.homeName}}</div>
                </div>
                <div class="gameInfo">
                  <div class="gameName">
                    <span>
                      {{game.matchDesc?game.matchDesc.substring(0,10).replace('世界杯',''):game.matchDesc}}
                    </span>
                    <span v-if="(game.awayName&&game.awayLogo&&game.awayName!=' ')&&game.officialNum" class="color-red">竞足{{game.officialNum}}</span>
                  </div>
                  <div class="score" v-if="game.state!=1">
                    {{game.homeGoals?game.homeGoals:0}}-{{game.awayGoals?game.awayGoals:0}}
                  </div>
                  <div v-else class="time"><span>{{getMatchDate(game.matchBeginTime)}}</span></div>
                  <div class="state">
                    <div v-if="!game.show" class="order">预约</div>
                    <div v-else>
                      <div v-if="game.state==2" class="live">直播中</div>
                      <div v-if="game.state==3" class="end">已结束</div>
                    </div>
                  </div>
                </div>
                <div class="team">
                  <img class="teamLogo" v-if="hasLogo(game.awayLogo)" v-lazy="game.awayLogo" alt="">
                  <img class="teamLogo" v-else src="../../../assets/images/worldCup/defalut.png" alt="">
                  <div class="teamName">{{game.awayName}}</div>
                </div>
              </div>
            </a>
          </div>
          <listBan :ListBan="ListBan" v-if="game.banner"></listBan>
        </li>
        <li class="loadmore" v-show="nomorelist">
          <div class="nodata" v-if="nodata"></div>
          <a v-else href="javascript:void (0)" @click="loadmore">
            <img src="../../../assets/images/loading.gif" class="loading" v-if="loadTxt=='正在加载中…'"/>
            <span>{{ loadTxt }}</span>
          </a>
        </li>
        <li v-if="nodata">
          <div class="nodata" v-if="nodata"><img src="../../../assets/images/nodata.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播"/></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  import * as utils from '../../../common/js/utils'
  import listBan from '../listBan.vue'
  import review from './worldCupReview.vue'
  export default{
    name: 'matchList',
    props:{
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    components:{listBan,review},
    data() {
      return {
        gamelist: [],
        page:{
          page:0,
          pageSize:15
        },
        nomorelist:true,
        loadTxt:'正在加载中…',
        scrollState:true,
        canload:true,
        img:{},
        nodata:false,
        time:'',

        isqd:'',

        oldTime:'',

        isWorldCup:true,
        boxStyle:{
          minHeight:'100%'
        },
        isReview: true
      }
    },
    created() {
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):''
      console.log(this.isqd)
      /*this.getToday();*/
      this.initData();
      this.scrollload();
      this.$nextTick(()=>{
        let htmlFont = 100 * (document.body.clientWidth / 750)
        let htmlHeight = document.body.clientHeight
        let temp = (0.96+1.88+0.76+.6)*htmlFont
        this.boxStyle.minHeight = (htmlHeight-temp)+'px'

      })
    },
    methods: {
      initData(){
        this.page= {
          page:0,
          pageSize:10,
          type : 0
        };
        this.loadTxt ='正在加载中…';
        let toData = {
          url:'worldcup/list',
          postData:this.page,
          callback:(res)=>{
            /*console.log(res)*/
            if (res.error_code === 1) {
              /*console.log(res)*/
              let list = res.data;

              if(list.length>0){
                this.oldTime = list[0].matchBeginTime
                this.gamelist = this.joinDatalist(list)
                this.gamelist[0].type = 'date'

                if(list.length>=7){
                  this.gamelist[6].banner = true
                }
              }
              if(list.length>0&&list.length<this.page.pageSize){
                this.nomorelist = false;
                this.nodata = false;
              }else if(list.length==0){
                this.nomorelist = true;
                this.loadTxt = '暂时没有数据'
                this.nodata = true;
              }
            }
          },
          error:()=>{
            console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      },

      scrollload(){
        var _that = this;
        window.onscroll = function (event) {

          if(_that.isWorldCup){
            var wScrollY = window.scrollY; // 当前滚动条位置
            var wInnerH = window.innerHeight; // 设备窗口的高度（不会变）
            var bScrollH = document.body.scrollHeight; // 滚动条总高度
            /*console.log('get in scrollload')*/
            if (wScrollY + wInnerH >= bScrollH-50&&_that.scrollState&&_that.canload) {
              /*console.log(wScrollY + '--' + wInnerH +'+=='+(wScrollY + wInnerH) + '--' + bScrollH)*/
              _that.scrollState = false;
              _that.loadmore();
              setTimeout(function () {
                _that.scrollState = true;
              },1000)
            }
          }
        }
      },

      loadmore(){
        this.nomorelist = true;
        if(this.canload){
          this.page.page = this.page.page - 0 + 1;
          let param = this.page;
          this.loadTxt ='正在加载中…';
          let toData = {
            url:'worldcup/list',
            postData:param,
            callback:(res) => {
              if (res.error_code === 1) {
                if(res.data.length>0){
                  let list = res.data

                  list = this.joinDatalist(list)
                  this.gamelist.push(...list);
                  this.nomorelist = false;
                  if(res.data.length<this.page.pageSize){
                    this.canload = false;
                    this.nomorelist = true;
                    this.loadTxt = '没有更多数据了';
                  }
                }else{
                  this.canload = false;
                  this.nomorelist = true;
                  this.loadTxt = '没有更多数据了';
                }
              }
            },

          }
          utils.cpost(this,toData);
        }
      },

      joinDatalist(list){
        // console.log(list);
        list.map((item,index)=>{
          // console.log(this.oldTime.split(' ')[0],item.matchBeginTime.split(' ')[0],this.oldTime.split(' ')[0]==item.matchBeginTime.split(' ')[0])
          if(this.oldTime.split(' ')[0]!=item.matchBeginTime.split(' ')[0]) {

            if(item.awayLogo){
              this.oldTime = item.matchBeginTime
              item.type = 'date'
            }else{
              item.type = 'macth'
            }
          }
          /*if(index== 6 || index == 13){
            item.banner = true
          }else{
            item.banner = false
          }*/

        })
        return list
      },
      getMatchDate(time){
//        time = utils.dateFormate(time,'yyyy-MM-dd hh:mm:ss')
        if(time){
          let str = time.split(' ')[1];
          let arr = str.split(':');
          arr.pop();
          time = arr.join(':');
        }
        return time
      },
      getDateWeek(time){
        let str = time
        if(time){
          let ymd = time.split(' ')[0]
          let iosd = time.replace(/-/g,'/');
          let d = (new Date(iosd)).getDay();
          let strd = ''

          switch (d){
            case 0:
              strd = '星期日';
              break;
            case 1:
              strd = '星期一';
              break;
            case 2:
              strd = '星期二';
              break;
            case 3:
              strd = '星期三';
              break;
            case 4:
              strd = '星期四';
              break;
            case 5:
              strd = '星期五';
              break;
            case 6:
              strd = '星期六';
              break;
          }

          str = ymd + ' ' + strd
        }
        return str
      },
      hasLogo(url){
        if(url){
          if(url.indexOf('http')>=0){
            return true
          }else{
            return false
          }
        }else{
          return false
        }
      }
    },
    beforeDestroy(){
      this.isWorldCup = false
    }
  }
</script>
<style lang="less" scoped>
  /*@import "worldCup.less";*/
</style>




